import React, { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { Squash as Hamburger } from 'hamburger-react'

function Header() {
  const [isOpen, setOpen] = useState(false);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  const setActiveLink = ({ isActive, isPending }) => isPending ? 'pending' : isActive ? 'link link_active' : 'link';

  useEffect(() => {
    window.addEventListener('resize', (evt) => setWindowWidth(evt.target.innerWidth))
  });

  return (
    <header className={`header ${isOpen && 'header_fix'}`}>
      <NavLink
        to='/'
        className='header__logo link'
        onClick={() => setOpen(false)}
      ></NavLink>
      <Hamburger
        toggled={isOpen}
        toggle={setOpen}
        size={windowWidth > 320 ? 44 : 20}
        direction="right"
      />
      <nav className={`header__navigation ${isOpen && 'header__navigation_active'}`}>
        <ul className='header__menu list'>
          {isOpen
            ? <>
              <li>
                <NavLink
                  to='/loft-bed-adult'
                  className={setActiveLink}
                  onClick={() => setOpen(false)}
                >- </NavLink>
              </li>
              <li>
                <NavLink
                  to='/loft-bed-children'
                  className={setActiveLink}
                  onClick={() => setOpen(false)}
                >- </NavLink>
              </li>
              <li>
                <NavLink
                  to='/bunk-bed'
                  className={setActiveLink}
                  onClick={() => setOpen(false)}
                > </NavLink>
              </li>
              <li>
                <NavLink
                  to='/tables'
                  className={setActiveLink}
                  onClick={() => setOpen(false)}
                ></NavLink>
              </li>
              <li>
                <NavLink
                  to='/other'
                  className={setActiveLink}
                  onClick={() => setOpen(false)}
                > </NavLink>
              </li>
              <li>
                <NavLink
                  to='/price'
                  className={setActiveLink}
                  onClick={() => setOpen(false)}
                ></NavLink>
              </li>
              <li>
                <NavLink
                  to='/contacts'
                  className={setActiveLink}
                  onClick={() => setOpen(false)}
                ></NavLink>
              </li>
            </>
            : <>
              <li>
                <NavLink
                  to='/price'
                  className={setActiveLink}
                  onClick={() => setOpen(false)}
                ></NavLink>
              </li>
              <li>
                <div className='dropdown'>
                  <button type='button' className='dropdown__button'></button>
                  <ul className='dropdown__menu list'>
                    <li>
                      <NavLink
                        to='/loft-bed-adult'
                        className={setActiveLink}
                        onClick={() => setOpen(false)}
                      >- </NavLink>
                    </li>
                    <li>
                      <NavLink
                        to='/loft-bed-children'
                        className={setActiveLink}
                        onClick={() => setOpen(false)}
                      >- </NavLink>
                    </li>
                    <li>
                      <NavLink
                        to='/bunk-bed'
                        className={setActiveLink}
                        onClick={() => setOpen(false)}
                      > </NavLink>
                    </li>
                    <li>
                      <NavLink
                        to='/tables'
                        className={setActiveLink}
                        onClick={() => setOpen(false)}
                      ></NavLink>
                    </li>
                    <li>
                      <NavLink
                        to='/other'
                        className={setActiveLink}
                        onClick={() => setOpen(false)}
                      > </NavLink>
                    </li>
                  </ul>
                </div>
              </li>
              <li>
                <NavLink
                  to='/contacts'
                  className={setActiveLink}
                  onClick={() => setOpen(false)}
                ></NavLink>
              </li>
            </>
          }
        </ul>
      </nav>
    </header>
  );
}

export default Header;
